// 吊顶组件
<template>
  <div id="condoleTop">
    <div class="left">
      <el-breadcrumb separator="/" v-if="currentRoutes.length > 1">
        <el-breadcrumb-item>
          <router-link :to="currentRoutes[0].path">{{
            currentRoutes[0].meta.title
          }}</router-link>
        </el-breadcrumb-item>
        <el-breadcrumb-item>
          <router-link :to="currentRoutes[1].path">{{
            currentRoutes[1].meta.title
          }}</router-link>
        </el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="right">
      <el-dropdown>
        <span class="el-dropdown-link">
          {{ $store.state.userInfo.username
          }}<i class="el-icon-arrow-down el-icon--right"></i>
        </span>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item class="dropdown-item" @click.native="logout"
            >退出</el-dropdown-item
          >
        </el-dropdown-menu>
      </el-dropdown>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      currentRoutes: this.$router.currentRoute.matched,
    };
  },
  watch: {
    $route() {
      // console.log(this.$router);
      this.currentRoutes = this.$router.currentRoute.matched;
    },
  },
  methods: {
    logout() {
      // 退出登录
      // console.log(1);
      this.$store.dispatch('asyncLogout');
      this.$router.push({
        name: 'Login',
      });
    },
  },
  created() {},
  mounted() {},
};
</script>
