<!-- 登录界面 -->
<template>
  <div id="login">
    <el-form
      :model="ruleForm"
      status-icon
      :rules="rules"
      ref="ruleForm"
      label-width="100px"
      class="demo-ruleForm login-form"
    >
      <el-form-item label="邮箱" prop="email" :required="true">
        <el-autocomplete
          v-model="ruleForm.email"
          placeholder="Email"
          :fetch-suggestions="querySearchEmail"
          :trigger-on-focus="false"
          class="form-email"
        ></el-autocomplete>
      </el-form-item>
      <el-form-item label="密码" prop="password" :required="true">
        <el-input
          v-model="ruleForm.password"
          type="password"
          placeholder="Password"
        ></el-input>
      </el-form-item>
      <el-form-item class="form-btn">
        <el-button type="primary" @click="submitForm('ruleForm')"
          >提交</el-button
        >
        <el-button @click="resetForm('ruleForm')">重置</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
// 导入关于登录页面的 API 接口
import userLogin from '@/api/login';

export default {
  data() {
    const checkEmail = (rule, value, callback) => {
      const emailReg = /\w+([-+.]\w+)*@\w+([-.]\w+)*\.\w+([-.]\w+)*/;
      if (!value) {
        return callback(new Error('邮箱不能为空'));
      }

      if (emailReg.test(value)) {
        return callback();
      }
      return callback(new Error('邮箱格式不正确'));
    };
    const validatePass = (rule, value, callback) => {
      if (value === '') {
        return callback(new Error('请输入密码'));
      }
      return callback();
    };
    return {
      ruleForm: {
        email: '',
        password: '',
      },
      rules: {
        email: [{ validator: checkEmail, trigger: 'blur', required: true }],
        password: [
          { validator: validatePass, trigger: 'blur', required: true },
        ],
      },
      submitKey: true,
    };
  },
  methods: {
    submitForm(formName) {
      this.$refs[formName].validate(async (valid) => {
        if (valid && this.submitKey) {
          // alert("submit!");
          this.submitKey = false;

          setTimeout(() => {
            this.submitKey = true;
          }, 2000);
          const res = await userLogin(this.ruleForm);
          // console.log(res);
          if (res.status !== 'success') {
            return this.$message.error(res.msg);
          }
          this.$store.dispatch('asyncSetUserInfo', res.data);

          // console.log(this.ruleForm);
          return this.$router.push({
            name: 'Home',
          });
        }
        // return console.log('error submit!!');
        return this.$message.warning('您点击过于频繁，请稍后再试');
      });
    },
    resetForm(formName) {
      this.$refs[formName].resetFields();
    },
    // 邮箱自动填充后缀名
    querySearchEmail(queryString, callback) {
      const emailList = [
        { value: '@qq.com' },
        { value: '@126.com' },
        { value: '@163.com' },
        { value: '@sina.com' },
        { value: '@21cn.com' },
        { value: '@sohu.com' },
        { value: '@yahoo.com.cn' },
        { value: '@tom.com' },
        { value: '@etang.com' },
        { value: '@eyou.com' },
        { value: '@56.com' },
        { value: '@x.cn' },
        { value: '@chinaren.comsogou.com' },
        { value: '@citiz.com' },
      ];
      let results = [];
      const queryList = [];
      emailList.map((item) => {
        queryList.push({ value: queryString.split('@')[0] + item.value });
        return true;
      });
      results = queryString
        ? queryList.filter(this.createFilter(queryString))
        : queryList;
      callback(results);
    },

    // 邮箱填写过滤
    createFilter(queryString) {
      return (item) => item.value.toLowerCase().indexOf(queryString.toLowerCase()) === 0;
    },
  },
};
</script>
<style lang="less" scoped>
@import url("~@/assets/css/login.less");
</style>
