const routes = [{
  path: '/',
  name: 'Home',
  redirect: '/calculate',
  component: Home,
  meta: {
    title: '首页',
  },
  children: [{
    path: 'calculate',
    name: 'Calculate',
    meta: {
      title: '统计',
    },
    component: () => import('../views/calculate.vue'),
  }],
},
{
  path: '/login',
  name: 'Login',
  component: Login,
},
{
  path: '/commodity',
  name: 'Commodity',
  redirect: '/commodity/list',
  meta: {
    title: '商品',
  },
  component: Home,
  children: [{
    path: 'list',
    name: 'List',
    meta: {
      title: '商品列表',
    },
    component: () => import('@/views/commodity/List.vue'),
  },
  {
    path: 'add',
    name: 'Add',
    meta: {
      title: '添加商品',
    },
    component: () => import('@/views/commodity/Add.vue'),
  },
  {
    path: 'edit/:id',
    name: 'Edit',
    meta: {
      title: '编辑商品',
    },
    component: () => import('@/views/commodity/Add.vue'),
  },
  ],
},
{
  path: '/about',
  name: 'About',
  // route level code-splitting
  // this generates a separate chunk (about.[hash].js) for this route
  // which is lazy-loaded when the route is visited.
  component: () => import(/* webpackChunkName: "about" */ '../views/About.vue'),
},
];

const router = new VueRouter({
  routes,
});

// console.log(store);
router.beforeEach((to, from, next) => {
  if (to.path !== '/login') {
    if (store.state.userInfo.username && store.state.userInfo.appkey && store.state.userInfo
      .role) {
      return next();
    }
    return next({
      name: 'Login',
    });
  }
  return next();
});

export default router;
