import Vue from 'vue';
import Vuex from 'vuex';
import * as cookie from '../utils/userCookie';

Vue.use(Vuex);

export default new Vuex.Store({
  state: {
    // userInfo: {
    //   username: '',
    //   appkey: '',
    //   role: '', // 角色 用来判断可以展示哪些列表
    //   email: '',
    // },
    userInfo: cookie.getUserCookie(),
  },
  mutations: {
    // 改变用户状态的方法
    setUserInfo(state, userInfo) {
      state.userInfo = userInfo;
    },
    // 退出登录
    logout(state) {
      state.userInfo = {
        username: '',
        appkey: '',
        role: '', // 角色 用来判断可以展示哪些列表
        email: '',
      };
    },
  },
  actions: {
    // 改变用户状态的方法
    asyncSetUserInfo(ctx, userInfo) {
      ctx.commit('setUserInfo', userInfo);
      cookie.setCookie(userInfo);
    },
    // 退出登录
    asyncLogout(ctx) {
      ctx.commit('logout');
      cookie.removeUserCookie();
    },
  },
  modules: {},
});
